type CardProps = {
  color: 'Blue' | 'Green' | 'Pink' | 'Yellow' | 'Rocket';
  value: number;
  onClick?: () => void;
  className?: string; // On ajoute √ßa pour pouvoir personnaliser depuis le parent
};

export default function Card({ color, value, onClick, className = "" }: CardProps) {
  const getColorStyles = () => {
    switch (color) {
      case 'Blue': return 'bg-blue-500 border-blue-700 text-white';
      case 'Green': return 'bg-green-600 border-green-800 text-white';
      case 'Pink': return 'bg-pink-500 border-pink-700 text-white';
      case 'Yellow': return 'bg-yellow-400 border-yellow-600 text-black';
      case 'Rocket': return 'bg-gray-800 border-gray-950 text-white';
      default: return 'bg-gray-200';
    }
  };

  return (
    <div 
      onClick={onClick}
      className={`
        ${getColorStyles()}
        ${className} 
        w-20 h-32 rounded-lg border-b-4 shadow-xl 
        flex flex-col items-center justify-center 
        cursor-pointer transition-all duration-200 ease-out
        relative select-none box-border
      `}
    >
      <span className="absolute top-1 left-1 text-xs font-bold opacity-80">{value}</span>
      <span className="text-4xl font-black font-mono">
        {color === 'Rocket' ? 'üöÄ' : value}
      </span>
      <span className="absolute bottom-1 right-1 text-xs font-bold opacity-80 rotate-180">{value}</span>
    </div>
  );
}